import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../App.css';

let urlImagen = 'https://sebasalmada.github.io/imgs/perfil.jpeg'


class Nav extends Component {
    state = { modo: 'Night Mode', check: true }

    _changeCheck = () => {
        if(this.state.check) {
            this.setState({ modo: 'Day Mode', check: false })
            document.documentElement.style.setProperty('--my-color', 'black')
            document.documentElement.style.setProperty('--my-bg-color', 'white')
            
        } else {
            this.setState({ modo: 'Night Mode', check: true })
            document.documentElement.style.setProperty('--my-color', 'rgb(54, 230, 48)')
            document.documentElement.style.setProperty('--my-bg-color', 'black')
            
        }
    }

    render () {
        return (
            <nav>
                <div>
                    <img className="fotoperfil" src={urlImagen}></img>
                    <div className="datos">
                        <div>Sebasti√°n Almada</div>
                        <div>typeOf(dev) = 'Fullstack'</div>
                        <div>//Buenos Aires, Argentina//</div>
                        <div className="redes">
                            <a href="https://www.linkedin.com/in/sebasalmada/" target="_blank"><i className="fab fa-linkedin"></i></a>
                            <a href="https://github.com/sebasalmada" target="_blank"><i className="fab fa-github"></i></a>
                            <a href="https://twitter.com/sebasalmada" target="_blank"><i className="fab fa-twitter"></i></a>
                            <a href="https://instagram.com/sebasalmada" target="_blank"><i className="fab fa-instagram"></i></a>
                        </div>
                    </div>
                </div>
                <div className="custom-control custom-switch">
                    <input type="checkbox" className="custom-control-input" id="customSwitch1" defaultChecked={this.state.check} onChange={this._changeCheck}/>
                    <label className="custom-control-label" htmlFor="customSwitch1">{this.state.modo}</label>
                </div>
            </nav>
        )
    }
}

export default Nav